import Richtext from "~/components/Richtext.vue";
import Breadbar from "~/components/Breadbar.vue";
export default {
  name: "homepage",
  components: [ Breadbar ],
  data() {
    return {
      types: [
        { text: 'Type1' },
        { text: 'Type2' },
      ],
      pageslug: this.$route.params.slug,
    }
  },
  methods: {
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
